import { oneOf } from 'clevertap-lib-common'
import Vue, { VNode } from 'vue'
import { Component, Prop } from 'vue-property-decorator'
import { CSSProperties } from 'vue/types/jsx'
import { BorderRadius, Color } from '../../utils/styles'
import { Icon } from '../icon/Icon'
import { OverflowableText } from '../OverflowableText/OverflowableText'
import { AnchoredPopup } from '../AnchoredPopup/AnchoredPopup'

import './Thumbnail.scss'
import { cx } from '../../utils/cx'

@Component({ name: 'Thumbnail' })
class Thumbnail extends Vue {
  static readonly EVENT_CLICK = 'click'
  private static readonly TRANSPARENTIZE_PERCENTAGE = 0.1
  private static readonly ALPHA = Math.round(256 * Thumbnail.TRANSPARENTIZE_PERCENTAGE).toString(16)

  static readonly TOOLTIP_OFFSET: number = 16
  static readonly SLOT_IMAGE = 'image'
  static readonly SLOT_ACTIONS = 'actions'

  readonly $slots: {
    [Thumbnail.SLOT_IMAGE]?: Array<VNode>
    [Thumbnail.SLOT_ACTIONS]?: Array<VNode>
  }

  @Prop({ type: Boolean, required: false, default: false })
  readonly loading: boolean

  @Prop({ type: String, required: true, validator: oneOf(Object.values(Icon.Type)) })
  readonly icon: Icon.Type

  @Prop({ type: String, required: false, validator: letterValidator })
  readonly letters: string

  @Prop({ type: String, required: false, default: Color.DARK30, validator: colorValidator })
  readonly color: string

  @Prop({ type: String, required: false, default: null, validator: colorValidator })
  readonly backgroundColor: string | null

  @Prop({
    type: Number,
    required: false,
    validator: oneOf(Object.values(BorderRadius).filter(v => typeof v === 'number')),
  })
  readonly borderRadius: number

  @Prop({ type: String, required: false, validator: oneOf(Object.values(Icon.Type)), default: null })
  readonly hoverIcon: Icon.Type | null

  @Prop({ type: String, required: false, default: null })
  readonly text: string | null

  @Prop({ type: String, required: false, default: null })
  readonly subText: string | null

  @Prop({ type: Number, required: false, default: 40 })
  readonly size: number

  @Prop({ type: Boolean, required: false, default: false })
  readonly hovered: boolean

  @Prop({ type: Boolean, required: false, default: false })
  readonly clickable: boolean

  @Prop({ type: Boolean, required: false, default: false })
  readonly transparentizeBackgroundColor: boolean

  @Prop({ type: [String, Object], required: false, default: null })
  readonly tooltip: string | null

  @Prop({ type: Boolean, required: false, default: false })
  readonly disabled: boolean

  @Prop({ type: Boolean, required: false, default: false })
  readonly showOutline: boolean

  @Prop({
    type: String,
    required: false,
    default (): AnchoredPopup.Placement {
      return AnchoredPopup.Placement.TOP
    },
  })
  readonly tooltipPlacement: AnchoredPopup.Placement

  @Prop({ type: Number, required: false, default: Thumbnail.TOOLTIP_OFFSET })
  readonly tooltipOffset: number

  private get thumbnailStyle (): Partial<CSSProperties> {
    return {
      width: `${this.size}px`,
      minWidth: `${this.size}px`,
      ...(this.$slots[Thumbnail.SLOT_IMAGE]
        ? { height: `${this.size}px`}
        // do not allow prolonged thumbnails when displaying images
        : { minHeight: `${this.size}px`}),
      ...(!this.$slots[Thumbnail.SLOT_IMAGE] && {
        // do not apply background color when displaying images
        backgroundColor: !this.disabled
          ? (this.hovered ? this.color : this.computedBackgroundColor)
          : Color.LIGHT10,
      }),
      color: !this.disabled
        ? (this.hovered ? Color.WHITE : this.color)
        : Color.DARK30,
      borderRadius: !this.borderRadius
        ? (this.size <= 20 ? '4px' : '8px')
        : `${this.borderRadius}px`,
    }
  }

  private get computedBackgroundColor (): string | undefined {
    if (!this.backgroundColor) {
      return `${this.color}${Thumbnail.ALPHA}`
    }

    return `${this.backgroundColor}${this.transparentizeBackgroundColor ? Thumbnail.ALPHA : ''}`
  }

  private get detailsStyle (): Partial<CSSProperties> {
    return {
      minHeight: `${this.size}px`,
    }
  }

  private get hasDetails (): boolean {
    return Boolean(this.text || this.subText)
  }

  private get thumbnailImageClasses (): string {
    return cx(
      'lp-thumbnail-image',
      {
        'lp-thumbnail-outline': this.showOutline,
      },
    )
  }

  render (): VNode {
    if (!this.hasDetails) {
      return this.renderThumbnail()
    }

    return (
      <div class="lp-thumbnail" onClick={() => { this.thumbnailClick() }}>
        {this.renderThumbnail()}
        <div class="lp-thumbnail-details" style={this.detailsStyle}>
          {this.text && <OverflowableText class="lp-thumbnail-title" text={this.text} />}
          {this.$slots[Thumbnail.SLOT_ACTIONS] && <div class="lp-thumbnail-actions">
            {this.$slots[Thumbnail.SLOT_ACTIONS]}
          </div>}
          {this.subText && <OverflowableText class="lp-thumbnail-subtitle" text={this.subText} />}
        </div>
      </div>
    )
  }

  private renderThumbnail (): VNode {
    if (this.loading) {
      return (
        <div class="thumbnail-loading-placeholder" style={this.thumbnailStyle} />
      )
    }

    if (this.$slots[Thumbnail.SLOT_IMAGE]) {
      return this.renderThumbnailImage()
    } else {
      return this.letters ? this.renderThumbnailLetters() : this.renderThumbnailIcon()
    }
  }

  private renderThumbnailLetters (): VNode {
    return (
      <div class="lp-thumbnail-letters" style={this.thumbnailStyle}>
          {this.letters}
      </div>
    )
  }

  private renderThumbnailImage (): VNode {
    return (
      <div class={this.thumbnailImageClasses} style={this.thumbnailStyle}>
        {this.$slots[Thumbnail.SLOT_IMAGE]}
      </div>
    )
  }

  private renderThumbnailIcon (): VNode {
    return (
      <div class="lp-thumbnail-icon" style={this.thumbnailStyle}>
        <Icon
          padding={(this.size - Icon.DEFAULT_SIZE_NUM) / 2}
          size={this.size}
          type={(this.hovered && this.hoverIcon) || this.icon}
          tooltip={this.tooltip}
          tooltipPlacement={this.tooltipPlacement}
          tooltipOffset={this.tooltipOffset}
          clickable={this.clickable}
          onClick={this.thumbnailClick}
        />
      </div>
    )
  }

  private thumbnailClick (): void {
    if (this.clickable) {
      this.$emit(Thumbnail.EVENT_CLICK)
    }
  }
}

function letterValidator (value: string): boolean {
  // matches 1- or 2-letter strings
  return /^[a-zA-Z]{1,2}$/.test(value)
}

function colorValidator (value: string): boolean {
  return /^#[A-Fa-f0-9]{6}$/.test(value)
}

export { Thumbnail }
